import React, { useRef, useEffect } from 'react';
import CodeMirror from 'codemirror'

import './App.css';

function App() {
  const ref = useRef<HTMLTextAreaElement>(null)

    useEffect(() => {
    if (!ref.current) return

    let cm = CodeMirror.fromTextArea(ref.current, {
      lineNumbers: true,
      readOnly: false,
      mode: 'hosts',
    })
  }, [])

  return (
    <div className="App">
      <textarea ref={ref}></textarea>
    </div>
  );
}

export default App;
